.button {
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      background: #40B0A5;
      border: none;
      border-radius: 4px;
      color: white;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      width: 222px;
      height: 100%;
}